// Input as in Desktop Index - LiveScores section, or inline as in Desktop Search - sidebar
$('.calendar-trigger-input, .calendar-inline').datepicker({
    language: 'de',             // Calendar localization (imported in app_*.js)
    position: 'bottom right',   // Position of calendar relative to input trigger
    autoClose: true,            // Close calendar after date selection

    // Do this after date selection
    onSelect(formattedDate, date, inst) {
        
    }
});

// Range dates, as in 320 Search
$('.calendar-trigger-range').datepicker({
    range: true,
    language: 'de',             // Calendar localization (imported in app_*.js)
    position: 'bottom right',   // Position of calendar relative to input trigger
    autoClose: true,            // Close calendar after date selection

    // Do this after date selection
    onSelect(formattedDate, date, inst) {

        // Split returned date in two UI inputs (from, to)
        var dates = formattedDate.split(',');
        // Wait for second date selection
        if (date.length === 2) {
            $('#calendar-from').val(dates[0]);
            $('#calendar-to').val(dates[1]);
        }

        // AJAX
    }
});

