import * as breakpoints from '../breakpoints';

var widget_tabs = {
    init: function(){
        this.cacheDom();
        this.startTabs();
        this.bindEvents();
    },
    cacheDom: function(){
        this.content = $('.tab-content');
        this.verticalNewsNav = $('.widget-news-vertical .tab-nav a');
    },
    startTabs: function(){
        this.content.tabbedContent();
    },
    bindEvents: function(){
        this.content.on('tabcontent.switch', this.loadContent);
        this.verticalNewsNav.on('mouseover', this.triggerNews);
    },
    triggerNews: function(event){
        // Triger click event on hover
        $(this).trigger('click');
    },
    loadContent: function(event, tab, api){
        // Handle AJAX events - i.e. load tab content
        // 'tab' holds tab id
        
    }
};
widget_tabs.init();