import * as breakpoints from '../breakpoints';

// LiveScores date carousel
var carousel_livescore = {
    init: function(){
        carousel_livescore.cacheDom();
        carousel_livescore.bindEvents();
        // Keep indivudual instances, for the purpose of arrow navigation, etc.
        carousel_livescore.wrap.each( carousel_livescore.startCarousel );
    },
    cacheDom: function(){
        carousel_livescore.wrap = $('.widget-carousel-livescore-dates');
        carousel_livescore.date = carousel_livescore.wrap.find('.entry');
        carousel_livescore.activeClass = 'active-date';
    },
    bindEvents: function(){
        // Select a different date
        carousel_livescore.date.on('click touchstart', this, this.changeDate);
    },
    startCarousel: function(){
        // Configure carousel content, arrows
        var thisConfig = carousel_livescore.carouselConfig($(this));
        thisConfig.carousel.slick(thisConfig);
    },
    carouselConfig: function($object){
        var carousel = $object.find('.content');
        var prevArrow = $object.find('.arrows .prev');
        var nextArrow = $object.find('.arrows .next');
        // 7 slides for large device, 5 slides for mobile device
        var numberOfSlides = $(window).width() >= breakpoints.width_md ? 7 : 5;

        var config = {
            carousel: carousel,
            slidesToShow: numberOfSlides,
            slidesToScroll: 3, 
            prevArrow: prevArrow,
            nextArrow: nextArrow,
            infinite: false,
        };

        return config;
    },
    changeDate: function(event){
        event.preventDefault();
        // Deselect the already active date
        carousel_livescore.date.removeClass(carousel_livescore.activeClass);
        // Add to clicked
        $(this).addClass(carousel_livescore.activeClass);

        // AJAX loading
        
    }
};
carousel_livescore.init();