var competitions_accordion = {
    init: function(){
        competitions_accordion.cacheDom();
        competitions_accordion.accordion.each( competitions_accordion.bindEvents );
        competitions_accordion.close();
    },
    cacheDom: function(){
        competitions_accordion.accordion = $('.widget-team-accordion');
        competitions_accordion.content = competitions_accordion.accordion.find('.section-content');
    },
    bindEvents: function(){
        var config = competitions_accordion.cacheEach( $(this) );
        config.trigger.on('click touchstart', config, competitions_accordion.toggle);
    },
    cacheEach: function($object){
        var config = {
            parent: $object,
            trigger: $object.find('.section-header'),
            openClass: 'open'
        }

        return config;
    },
    toggle: function($object){
        $object.preventDefault();
        var config =  $object.data;
        var thisGroup = $(this).next();
                
        $(this).toggleClass(config.openClass);
        thisGroup.slideToggle();
    },
    close: function(){
        competitions_accordion.content.slideUp();
    }
};
competitions_accordion.init();