import * as breakpoints from '../breakpoints';

// Gallery page - thumbnails carousel
var carousel_gallery_thumbs = {
    init: function(){
        this.cacheDom();
        this.startCarousel();
        this.bindEvents();
    },
    cacheDom: function(){
        this.wrap = $('.carousel-gallery-thumbs');
        this.carousel = this.wrap.find('.content');
        this.prevArrow = this.wrap.find('.arrows .prev');
        this.nextArrow = this.wrap.find('.arrows .next');
        this.loadMore = this.wrap.find('.widget-view-more-load');
        this.activeClass = 'thumbs-opened';
        
    },
    startCarousel: function(){
        var config = {
            slidesToShow: 5,
            slidesToScroll: 1,  
            prevArrow: this.prevArrow,
            nextArrow: this.nextArrow
        };

        this.carousel.slick(config);
    },
    bindEvents: function(){
        // Expand or contract thumbs view
        this.loadMore.on('click', this, this.toggleThumbs)
    },
    toggleThumbs: function(event){
        var self = event.data;
        event.preventDefault();

        var areThumbsExpanded = self.loadMore.hasClass(self.activeClass)
        if ( areThumbsExpanded ) {
            // Contract thumbs - re-initialize carousel
            self.startCarousel();
        } else {
            // Expand thumbs - destroy carousel
            self.carousel.slick('unslick');
        }

        // Switch button
        self.wrap.toggleClass(self.activeClass);
        self.loadMore.toggleClass(self.activeClass);
    }
};
carousel_gallery_thumbs.init();