var widget_standings_accordion = {
    init: function(){
        widget_standings_accordion.cacheDom();
        widget_standings_accordion.accordion.each( widget_standings_accordion.bindEvents );
    },
    cacheDom: function(){
        widget_standings_accordion.accordion = $('.st-table-accordion');
    },
    bindEvents: function(){
        var config = widget_standings_accordion.cacheEach( $(this) );
        config.trigger.on('click touchstart', config, widget_standings_accordion.toggle);
    },
    cacheEach: function($object){
        var config = {
            parent: $object,
            trigger: $object.find('.st-table-group'),
            openClass: 'open'
        }

        return config;
    },
    toggle: function($object){
        $object.preventDefault();
        var config =  $object.data;
        var thisGroup = $(this);

        thisGroup.toggleClass(config.openClass);
    }
};
widget_standings_accordion.init();