var widget_prediction = {
	init: function(){
		widget_prediction.cacheDom();
		widget_prediction.$entry.each(widget_prediction.bindEvents);
	},
	cacheDom: function(){
		widget_prediction.$wrap = $('.widget-prediction');
		widget_prediction.$entry = widget_prediction.$wrap.find('.prediction-entry');
	},
	cacheEach: function($object){
		var panel = {
			parent: $(this),
			input: $object.find('.prediction-input'),
			btnUp: $object.find('.prediction-input-up'),
			btnDown: $object.find('.prediction-input-down')
		}
		return panel;
	},
	bindEvents: function(){
		var panel = widget_prediction.cacheEach($(this));
		panel.btnUp.on('click', panel, widget_prediction.numIncrease);
		panel.btnDown.on('click', panel, widget_prediction.numDecrease);
	},
	numIncrease: function(event){
		var panel = event.data;
		panel.input.val(function(i, currentValue){
			return ++currentValue;
		});
	},
	numDecrease: function(event){
		var panel = event.data;
		var currentValue = panel.input.val();
		
		// Accent only positive values
		if ( currentValue > 0 ) {
			panel.input.val(function(i, currentValue){
				return --currentValue;
			});
		}
		
	}
};
widget_prediction.init();