import * as breakpoints from '../breakpoints';

// 5-item carousel
var carousel_5 = {
    init: function(){
        carousel_5.cacheDom();
        // Keep indivudual instances, for the purpose of arrow navigation, etc.
        carousel_5.wrap.each( carousel_5.startCarousel );
    },
    cacheDom: function(){
        carousel_5.wrap = $('.widget-carousel-5');
    },
    startCarousel: function(){
        // Configure carousel content, arrows
        var thisConfig = carousel_5.config($(this));
        thisConfig.carousel.slick(thisConfig);
    },
    config: function($object){
        var carousel = $object.find('.content');
        var prevArrow = $object.find('.arrows .prev');
        var nextArrow = $object.find('.arrows .next');

        var config = {
            carousel: carousel,
            slidesToShow: 5,
            slidesToScroll: 1,  
            prevArrow: prevArrow,
            nextArrow: nextArrow
        };

        return config;
    }
};
carousel_5.init();